<template>
  <div class="trial-page">

    <!-- 订阅成功后显示内容 -->
    <success-message class="success-msg" v-if="completed"
      :countDown="0"
      :title="$t('login.trial.apply_suc_title')">
      {{ $t('login.trial.apply_suc_tip1') }}
      {{ $t('login.trial.apply_suc_tip2') }}
    </success-message>

    <template v-else>
      <img
        :src="logo"
        class="logo"
        :class="{'logo-anta':appName === 'anta'}"
        :alt="$t('login.index.logo_alt')"
        @click="$router.push({ name: 'Login' })" />
      <div class="trial-content">
        <h1 class="trial-title">{{ $t('login.trial.start_trial') }}</h1>

        <form-trial @validate="submit" ref="formTrial" />

        <div class="trial-bottom">
          <el-button
            type="primary"
            @click="handleSubmit">{{ $t('public.form.submit') }}</el-button>

          <back-login :text="$t('login.already_account')"/>
        </div>
      </div>
      <!-- <BusinessFootNote v-if="appName === 'anta'"/> -->
    </template>
  </div>
</template>

<script>
import mixinLoading from '@/mixins/loading';
import { applyAccountForTrial } from '@/api/account';
import envs from '../../../utils/envs';
import SuccessMessage from '../components/SuccessMessage';
import BackLogin from '../components/BackLogin';
// import BusinessFootNote from '../components/BusinessFootNote.vue';
import FormTrial from './components/FormTrial';


export default {
  components: {
    SuccessMessage,
    FormTrial,
    BackLogin,
    // BusinessFootNote,
  },
  mixins: [mixinLoading],
  data() {
    return {
      completed: false,
    };
  },
  computed: {
    logo() {
      return envs.VUE_APP_COOPERATION === 'anta' ? require('@/assets/img/login/logo-anta.png')
        : require('@/assets/img/login/logo-register.png');
    },
    appName() {
      return envs.VUE_APP_COOPERATION;
    },
  },
  methods: {
    async submit(form) {
      // 当发送请求前，添加 Loading 状态
      this.startLoading();

      try {
        await applyAccountForTrial({ ...form, email: form.email.toLowerCase() });
        this.completed = true;
      } catch (err) {
        this.$errHandler(err);
      } finally {
        this.endLoading();
      }
    },
    handleSubmit() {
      this.$refs.formTrial.validateForm();
    },
  },
};
</script>

