  position: relative;
  padding: 0 40px 40px;
  min-height: 480px;

  .logo {
    margin: 40px 0 40px -16px;
    height: 24px;
    cursor: pointer;
    &.logo-anta {
      height: 19px;
      margin: 44px 0 40px 0;
    }
  }
  .trial-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;

    .trial-title {
      margin-bottom: 20px;
      width: 100%;
      font-size: 16px;
      text-align: left;
    }
    .trial-bottom {
      width: 320px;
      .el-button {
        margin: 10px 0 20px;
        width: 100%;
      }
    }
  }
  .success-msg {
    margin-top: 80px;
    height: 280px;
  }
}
</style>
