<template>
  <el-form
    :model="form"
    :rules="rules"
    size="medium"
    label-width="auto"
    ref="form">

    <div class="clearfix">
      <el-form-item prop="firstName" class="first" :label="$t('login.name') + ':'">
        <el-input v-model="form.firstName" :placeholder="$t('login.first_name')" clearable />
      </el-form-item>

      <el-form-item prop="lastName" class="last" label-width="20px">
        <el-input v-model="form.lastName" :placeholder="$t('login.last_name')" clearable />
      </el-form-item>
    </div>

    <el-form-item prop="position" :label="$t('login.position') + ':'">
      <el-input
        v-model="form.position"
        :placeholder="$t('login.enter_occupation')"
        clearable />
    </el-form-item>

    <el-form-item prop="companyName" :label="$t('login.full_company_name') + ':'">
      <el-input
        v-model="form.companyName"
        :placeholder="$t('login.enter_company_name')"
        clearable />
    </el-form-item>

    <el-form-item prop="companyType" :label="$t('login.business_type') + ':'">
      <dictionary-selector
        type="company"
        :placeholder="$t('login.select_company_type')"
        @on-change="form.companyType = $event" />
    </el-form-item>

    <el-form-item prop="country" :label="$t('login.country') + ':'">
      <DictionaryCountrySelector @change="form.country = $event" />
    </el-form-item>

    <el-form-item prop="email" :label="$t('login.email') + ':'">
      <el-input v-model.trim="form.email" :placeholder="$t('login.enter_email')" clearable />
    </el-form-item>

    <el-form-item prop="phone" :label="$t('login.phone') + ':'">
      <el-input v-model.trim="form.phone" :placeholder="$t('login.enter_phone')" clearable />
    </el-form-item>
  </el-form>
</template>

<script>
import i18n from 'i18next';
import DictionarySelector from '@/components/dictionary/DictionarySelector';
import DictionaryCountrySelector from '@/components/dictionary/DictionaryCountrySelector';

import { mixinValidateForm } from '@/mixins/form-validation';

import validator from '@/utils/validators/rules-validation';

export default {
  components: {
    DictionarySelector,
    DictionaryCountrySelector,
  },
  mixins: [mixinValidateForm],
  data() {
    return {
      form: {
        firstName: '',
        lastName: '',
        position: '',
        companyName: '',
        companyType: '',
        country: '',
        email: '',
        phone: '',
      },
      rules: {
        firstName: [
          validator.requirement(),
          { pattern: /^[\u4E00-\u9FA5A-Za-z0-9_\s]+$/, message: i18n.t('login.special_char_err') },
        ],
        lastName: [
          validator.requirement(),
          { pattern: /^[\u4E00-\u9FA5A-Za-z0-9_\s]+$/, message: i18n.t('login.special_char_err') },
        ],
        position: [
          validator.requirement(),
          ...validator.charLengthRange(2, 64),
          validator.charTypeNormal(),
        ],
        companyName: [
          ...validator.charLengthRange(2, 64),
        ],
        companyType: validator.selection(),
        country: validator.selection(),
        phone: [
          { pattern: /^[0-9-]{9,20}$/, message: i18n.t('login.numbers_rules_err') },
        ],
        email: validator.rules('requirement', 'email'),
      },
    };
  },
};
</script>

