<template>
  <el-select
    v-model="selectedValue"
    :placeholder="placeholder"
    :disabled="disabled"
    filterable
    clearable
    @change="$emit('change', selectedValue)">
    <el-option
      v-for="item in options"
      :key="item.id"
      :label="lang === 'cn' ? item.name : item.usName"
      :value="item.id" />
  </el-select>
</template>

<script>
import i18n from 'i18next';
import { getCountryList } from '@/api/dictionary';

export default {
  props: {
    // 选中的值
    value: {
      type: [String, Number],
      default: '',
    },
    placeholder: {
      type: String,
      default: i18n.t('login.country_palceholder'),
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    this.lang = i18n.language;
    return {
      selectedValue: '',
      options: [],
    };
  },
  async created() {
    try {
      const res = await getCountryList();
      this.options = res.data;
    } catch (err) {
      this.$message.error(err.message);
    }
  },
  watch: {
    value: {
      handler(val) {
        this.selectedValue = val || '';
      },
      immediate: true,
    },
  },
};
</script>
